import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import styles from './ProjectView.module.scss';

const ProjectView = () => {
  const [items, setItems] = useState([
    { name: "Project 1", des: "Description for Project 1", color: "#FF6B6B" },
    { name: "Project 2", des: "Description for Project 2", color: "#4ECDC4" },
    { name: "Project 3", des: "Description for Project 3", color: "#45B7D1" },
    { name: "Project 4", des: "Description for Project 4", color: "#FFA07A" },
    { name: "Project 5", des: "Description for Project 5", color: "#98D8C8" },
    { name: "Project 6", des: "Description for Project 6", color: "#F7B267" },
  ]);

  useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === 'ArrowRight') {
        handleNext();
      } else if (event.key === 'ArrowLeft') {
        handlePrev();
      }
    };

    window.addEventListener('keydown', handleKeyDown);

    return () => {
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, []);

  const handleNext = () => {
    setItems(prevItems => {
      const newItems = [...prevItems];
      newItems.push(newItems.shift());
      return newItems;
    });
  };

  const handlePrev = () => {
    setItems(prevItems => {
      const newItems = [...prevItems];
      newItems.unshift(newItems.pop());
      return newItems;
    });
  };

  return (
    <div className={styles.container}>
      <div className={styles.slide}>
        {items.map((item, index) => (
          <div key={index} className={styles.item} style={{backgroundColor: item.color}}>
            <div className={styles.content}>
              <div className={styles.name}>{item.name}</div>
              <div className={styles.des}>{item.des}</div>
              <button>See More</button>
            </div>
          </div>
        ))}
      </div>
      <div className={styles.button}>
        <button className={styles.prev} onClick={handlePrev}>
          <FontAwesomeIcon icon={faArrowLeft} />
        </button>
        <button className={styles.next} onClick={handleNext}>
          <FontAwesomeIcon icon={faArrowRight} />
        </button>
      </div>
    </div>
  );
};

export default ProjectView;
